import React from "react";
import "./index.scss";
import articleMenus from "../../articles/index";
import logoUrl from "../../assets/avatar.jpg"

function Home(props){

	const linkToDetail = (id) => {
		props.history.push(`/article/${id}`)
	}
	return (
		<div className="box">
			<div className="user-info">
				<img className="avatar" src={logoUrl} alt="lucas头像" />
				<p className="name">Lucas</p>
				<p className="desc">永远年轻，永远热泪盈眶</p>
				<p className="">邮箱：1501324336@qq.com</p>
				<p className="">微信：fu1501324336</p>
			</div>
			<div className="list">
				{articleMenus.map(article=>{
					return (
						<div className="article-box" key={article.id}>
							<p className="title" onClick={()=>linkToDetail(article.id)}>{article.title}</p>
							<p>
								<span className="create-time">{article.createTime}</span>
								<i className="icon"></i>
								{
									article.tags.map(tag=>{
										return (
											<span className="tag" key={tag}>{tag}</span>
										)
									})
								}
							</p>
						</div>
					)
				})}
			</div>
		</div>
	)
}
export default React.memo(Home);