import React,{useState} from "react";
import { Link } from "react-router-dom"
import avatar from "../../assets/avatar.jpg"
import './index.scss';

function NavBar(){
	const [ activeName, setActiveName ] = useState('home');
	const navs = [
		{
			title:"文章",
			path:"/home",
			name:"home"
		},
		{
			title:"分类",
			path:"/category",
			name:"category"
		},
		{
			title:"随笔",
			path:"/essay",
			name:"essay"
		},
		{
			title:"友情链接",
			path:"/links",
			name:"links"
		}
	]
	return (
		<div className="nav-bar">
			<div className="info">
				<img className="avatar" src={avatar} alt="lucas头像" />
				<span className="name">Lucas</span>
			</div>
			<div className="menus">
				{
					navs.map(nav=>{
						return (
							<Link 
								key={nav.title} 
								className={ `nav ${activeName === nav.name ? 'active' : '' }` } 
								to={nav.path} 
								onClick={() => setActiveName(nav.name)}
							>
								{nav.title}
							</Link>
						)
					})
				}
			</div>
		</div>
	);
}
export default React.memo(NavBar);

